.header {
  width: 100%;
  cursor: pointer;
  display: flex;
  justify-content: center;
  text-transform: uppercase;
  background-color: black;
  z-index: 100;
  position: fixed;
}
h1 {
  position: relative;
  font-family: "Montserrat", Arial, sans-serif;
  font-size: calc(20px + 3vw);
  font-weight: 300;
  color: #fff;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  text-shadow: 0 0 0.15em #bb1dc0;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  white-space: nowrap;
  -webkit-filter: blur(0.007em);
          filter: blur(0.007em);
  -webkit-animation: shake 2.5s linear forwards;
          animation: shake 2.5s linear forwards;
}

h1 span {
  position: absolute;
  top: 0;
  left: 0;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-clip-path: polygon(10% 0%, 44% 0%, 70% 100%, 55% 100%);
  clip-path: polygon(10% 0%, 44% 0%, 70% 100%, 55% 100%);
}

h1::before,
h1::after {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
}

h1::before {
  -webkit-animation: crack1 2.5s linear forwards;
          animation: crack1 2.5s linear forwards;
  -webkit-clip-path: polygon(0% 0%, 10% 0%, 55% 100%, 0% 100%);
  clip-path: polygon(0% 0%, 10% 0%, 55% 100%, 0% 100%);
}

h1::after {
  -webkit-animation: crack2 2.5s linear forwards;
          animation: crack2 2.5s linear forwards;
  -webkit-clip-path: polygon(44% 0%, 100% 0%, 100% 100%, 70% 100%);
  clip-path: polygon(44% 0%, 100% 0%, 100% 100%, 70% 100%);
}

@-webkit-keyframes shake {
  5%,
  15%,
  25%,
  35%,
  55%,
  65%,
  75%,
  95% {
    -webkit-filter: blur(0.018em);
            filter: blur(0.018em);
    -webkit-transform: translateY(0.018em) rotate(0deg);
            transform: translateY(0.018em) rotate(0deg);
  }

  10%,
  30%,
  40%,
  50%,
  70%,
  80%,
  90% {
    -webkit-filter: blur(0.01em);
            filter: blur(0.01em);
    -webkit-transform: translateY(-0.018em) rotate(0deg);
            transform: translateY(-0.018em) rotate(0deg);
  }

  20%,
  60% {
    -webkit-filter: blur(0.03em);
            filter: blur(0.03em);
    -webkit-transform: translate(-0.018em, 0.018em) rotate(0deg);
            transform: translate(-0.018em, 0.018em) rotate(0deg);
  }

  45%,
  85% {
    -webkit-filter: blur(0.03em);
            filter: blur(0.03em);
    -webkit-transform: translate(0.018em, -0.018em) rotate(0deg);
            transform: translate(0.018em, -0.018em) rotate(0deg);
  }

  100% {
    -webkit-filter: blur(0.007em);
            filter: blur(0.007em);
    -webkit-transform: translate(0) rotate(-0.5deg);
            transform: translate(0) rotate(-0.5deg);
  }
}

@keyframes shake {
  5%,
  15%,
  25%,
  35%,
  55%,
  65%,
  75%,
  95% {
    -webkit-filter: blur(0.018em);
            filter: blur(0.018em);
    -webkit-transform: translateY(0.018em) rotate(0deg);
            transform: translateY(0.018em) rotate(0deg);
  }

  10%,
  30%,
  40%,
  50%,
  70%,
  80%,
  90% {
    -webkit-filter: blur(0.01em);
            filter: blur(0.01em);
    -webkit-transform: translateY(-0.018em) rotate(0deg);
            transform: translateY(-0.018em) rotate(0deg);
  }

  20%,
  60% {
    -webkit-filter: blur(0.03em);
            filter: blur(0.03em);
    -webkit-transform: translate(-0.018em, 0.018em) rotate(0deg);
            transform: translate(-0.018em, 0.018em) rotate(0deg);
  }

  45%,
  85% {
    -webkit-filter: blur(0.03em);
            filter: blur(0.03em);
    -webkit-transform: translate(0.018em, -0.018em) rotate(0deg);
            transform: translate(0.018em, -0.018em) rotate(0deg);
  }

  100% {
    -webkit-filter: blur(0.007em);
            filter: blur(0.007em);
    -webkit-transform: translate(0) rotate(-0.5deg);
            transform: translate(0) rotate(-0.5deg);
  }
}

@-webkit-keyframes crack1 {
  0%,
  95% {
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }

  100% {
    -webkit-transform: translate(-51%, -48%);
            transform: translate(-51%, -48%);
  }
}

@keyframes crack1 {
  0%,
  95% {
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }

  100% {
    -webkit-transform: translate(-51%, -48%);
            transform: translate(-51%, -48%);
  }
}

@-webkit-keyframes crack2 {
  0%,
  95% {
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }

  100% {
    -webkit-transform: translate(-49%, -53%);
            transform: translate(-49%, -53%);
  }
}

@keyframes crack2 {
  0%,
  95% {
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }

  100% {
    -webkit-transform: translate(-49%, -53%);
            transform: translate(-49%, -53%);
  }
}

