.media {
  display: flex;
  flex-direction: column;
  width: 200px;
  padding: 5px;
  margin: 5px 0;
  background-color: #282c34;
  border-radius: 10px;
  position: relative;
}

.media:hover {
  background-color: #fff;
  color: #333;
  box-shadow: 0 0 3px #fff, 0 0 5px #fff, 0 0 11px #fff,
    0 0 22px rgb(203, 33, 255), 0 0 12px rgb(203, 33, 255),
    0 0 34px rgb(203, 33, 255), 0 0 32px rgb(203, 33, 255),
    0 0 56px rgb(203, 33, 255);
}

@media (max-width: 550px) {
  .media {
    width: 46%;
  }
}
.poster {
  border-radius: 10px;
}
.title {
  width: 100%;
  text-align: center;
  font-size: 17px;
  padding: 1rem 0;
}
.subTitle {
  display: flex;
  justify-content: space-between;
  padding: 0 2px;
  padding-bottom: 3px;
}

.ContentModal__landscape {
  object-fit: contain;
  border-radius: 10px;
}

.ContentModal__portrait {
  display: none;
  object-fit: contain;
  border-radius: 10px;
}

.tagline {
  padding-bottom: 10px;
  align-self: center;
}

.ContentModal {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  width: 100%;
  overflow-y: scroll;
  scrollbar-width: none;
  outline: none;
}

.ContentModal::-webkit-scrollbar {
  display: none;
}

.ContentModal__about {
  padding: 10px;
  width: 95%;
  height: 90%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  font-weight: 300;
}

.ContentModal__title {
  font-size: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.ContentModal__description {
  display: flex;
  height: 40%;
  overflow-y: scroll;
  padding: 15px;
  border-radius: 20px;
  scrollbar-width: thin; /* Firefox */

  text-align: justify;
}

.ContentModal__description::-webkit-scrollbar {
  display: none;
}

@media (min-width: 835px) {
  .ContentModal__landscape {
    display: none;
  }
  .ContentModal__portrait {
    display: flex;
    width: 38%;
  }
  .ContentModal {
    flex-direction: row;
    justify-content: space-around;
    padding: 10px 0;
  }
  .ContentModal__about {
    width: 58%;
    padding: 0;
    height: 100%;
  }
  .ContentModal__title {
    font-size: 24px;
  }
  .ContentModal__description {
    font-size: 18px;
  }
}

.carouselItem {
  display: flex;
  flex-direction: column;
  object-fit: contain;
  padding: 10px;
}
.carouselItem__img {
  border-radius: 10px;
  margin-bottom: 5px;
  box-shadow: 0px 0px 5px black;
}

.trending {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.MuiTabs-flexContainer {
  font-display: flex;
  flex-wrap: nowrap;
  width: 100%;
  align-items: center;
  justify-content: center;
}

